import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Conclusao_auditoria } from '../../../modelo/Conclusao_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'conclusao_auditoria-dados-details',   templateUrl: 'conclusao_auditoria_dadosDetails.component.html' }) export class Conclusao_auditoriaDadosDetailsComponent implements OnInit {    conclusao_auditoria: Conclusao_auditoria;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.conclusao_auditoria.Conclusao_auditoria";                 let id = +params['id'];         this.conclusao_auditoria = new Conclusao_auditoria();         this.conclusao_auditoria.cd_conclusao_auditoria = id;         o.objetoJson = this.conclusao_auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a conclusao_auditoria. '; }      this.conclusao_auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


